<template>
    <section>
        <Header title="Equipe" img="https://cdn.discordapp.com/attachments/294968413447782400/717317259349196890/Funko_Render_04_20.png" />
        <div class="container">
            <v-row>
                <v-col v-if="cards.length <= 0" sm="4">
                    <v-sheet dark>
                        <v-skeleton-loader type="card"></v-skeleton-loader>
                    </v-sheet>
                </v-col>
                <v-col v-for="(item, i) in cards" :key="i" sm="4">
                    <v-card dark shaped class="card-background text-center">
                        <v-img class="avatar" height="450" :src="item.photo" />
                        <h2 style="margin: 0.8em 0 0 0 ;">{{ item.name }}</h2>
                        <v-card-subtitle style="padding-top:0px;">{{ item.subtitle }}</v-card-subtitle>
                        <v-divider />
                        <v-card-text class="text-left">{{ item.description }}</v-card-text>
                        <v-divider />
                        <div class="pa-3">
                            <div class="text-center">
                                <v-tooltip v-for="(btn, t) in item.actions" :key="t" top>
                                    <template v-slot:activator="{ on, attrs }">
                                        <v-btn @click="openLink(btn.link)" icon v-on="on" v-bind="attrs">
                                            <v-icon>{{ btn.icon }}</v-icon>
                                        </v-btn>
                                    </template>
                                    <span>{{ btn.title }}</span>
                                </v-tooltip>
                            </div>
                        </div>
                    </v-card>
                </v-col>
            </v-row> 
        </div>
    </section>
</template>

<script>
import Header from '../componets/Header';

export default {
    name: "TeamPage",
    components: {
        Header
    },
    async mounted()
    {
        await window.axios.get('/team.json')
        .then((data) => {
            for(let object in data.data)
            {
                this.cards.push(data.data[object]);
            }
        });
    },
    data()
    {
        return {
            cards: []
        }
    },
    methods: {
        openLink(link)
        {
            window.open(link, '_blank');
        }
    }
}
</script>